












import Vue from 'vue';

import TheMobileHomeScreen from './components/TheMobileHomeScreen.vue';
import TheEnterNameScreen from './components/TheEnterNameScreen.vue';

export default Vue.extend({
  name: 'AppRemote',
  components: { TheMobileHomeScreen, TheEnterNameScreen },
  data: function () {
    return {
      state: 'welcome'
    }
  },
  methods: {
    joinGame: function(sessionId: string) {
      const vm = this as any

      vm.$socket.emit('join-session', sessionId, function (res: any) {
          if (res.error) {
              alert(res.error)
          } else {
              vm.state = 'joined-game';
              alert('Joined Game. You\'re: ' + res.username)
          }
      })
    }
  }
});
